@import "variables";

//================================ Component section
@mixin Component {
  padding: 40px 80px 80px;
  @media screen and (max-width: 500px) {
      padding: 40px 25px;
  }
}


@mixin DarkComponent {
  @include Component;
  background-color: $main-darkblue;
}

@mixin LightComponet {
  @include Component;
  background-color: $main-lightblue
}

// =================>   Logo section
@mixin Big-logo {
  width: 80px;
  font-size: 13px;
   img {
    width: 76%;
    height: auto;
    object-fit: contain;
    margin: 0px 12%;
  }
  .first {
    font-size: 0.9em;
    margin: 1px 0px 2px 12%;
    letter-spacing: 0.2px;
  }
  .second {
    font-size: 0.67em;
    margin: 0px;
  }
}

@mixin Small-Logo {
  // Logo
  .logo-div {
    width: 55px;
    font-size: 12px;
   img {
     width: 100%;
     margin: 0px;
   }
   .first {
     margin-left: 0px;
   }
   .second {
     font-size: 0.5em;
   }
  }
}

//=========== Burger
@mixin Burger {
  div {
    margin: 4px;
    width: 28px;
    height: 4px;
    border-radius: 2px;
    background-color: $main-font-color;
  }
}


//================================ Button section
@mixin Button {
  background-color: $body-back-color;
  color: inherit;
  letter-spacing: 0.5px;
  word-spacing: 2px;
  font-family: $font-SegoeProLight;
  font-size: 0.9rem;
  box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.22);

  &:hover {
    background-color: $button-hover;
    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.22);
  }
  &:active {
    background-color: $button-active;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.22);
  }
}

// ========================== blue Round section
@mixin Dark-round-div {
  border-radius: 50%;
  background-color: $main-darkblue;
}
@mixin Light-round-div {
  border-radius: 50%;
  background-color: $main-lightblue;
}

//======================Header anchors section
@mixin A-links {
  & a {
    display: inline-block;
    padding: 5px 15px;
    color: inherit;
    text-decoration: none;
  }
}

@mixin h1-p-header {
  h1 {
    font-size: 2.3em;
    margin-top: 0px;
    margin-bottom: 1.4em;
    width: 6em;
    span {
      color: $main-lightblue;
    }
  }
  p {
    font-size: 1em;
    margin-bottom: 2.5em;
    width: 18em;
    max-width: 100%;
    opacity: 0.95;
  }
}


// ==================== Plan section
@mixin h2-text {
  font-family: $font-OpenSans-Semibold;
  margin: 0px;
  display: inline-block;
  padding: 15px 33px;
  font-size: 20px;
  border: 1px solid;
  border-radius: 2px;
}

@mixin step-card {
  color: #FFFFFF;
  & a {
    text-decoration: none;
    color: inherit;
  }
  .step-img-div {
    margin: 30px auto;

    width: 210px;
    height: 210px;
    @include Dark-round-div;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .step-img-div div {
    margin-bottom: 40px;
  }
  .step-img-div p {
    font-family: $font-SegoeProLight;
    margin-bottom: 30px;
    font-size: 18px;
  }
  & > p {
    font-family: $font-SegoeProSemilightItalic;
  }
  & a {
    font-family: $font-SegoePro-LightItalic;
  }
  & span {
    border-bottom: 1px solid #FFFFFF;
  }
  & i {
    margin-left: 4px;
    color: #223E73;
  }
  //spesific
  .success > p {
    font-family: $font-SegoePro-BoldItalic;
  }
}
