@import "./abstracts/global";
@import "./sections/global";


body {
  font-size: 16px;
  padding: 0px $left-right;
  overflow-x: hidden;
  background-color: $body-back-color;
  color: $main-font-color;
  font-family: $font-OpenSans-Light;
}

.topShadow {
  background-color: $main-lightblue;
  height: $top-bottom;
  transform: translateX(24px) skew(-44deg);
  overflow-x: hidden;
}

.bottomShadow {
  background-color: $main-darkblue;
  height: $top-bottom;
  transform: translateX(24px) skew(44deg);
  overflow-x: hidden;
}

@media screen and (max-width: 1015px) {
  body {
    padding: 0px;
    background-color: white;
  }
  .bottomShadow,
  .topShadow {
    display: none;
  }
}

img {
  max-width: 100%;
}



.test {
  height: 100vh;
}
.main {
  background-color: rgba(77, 56, 232, 0.72);
  width: 400px;
  height: 400px;
  border-radius: 200px;
  margin: 100px auto;
  position: relative;
}

.circle1,
.circle2,
.circle3 {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  z-index: 3;
  transform: translateX(65px);
}

.circle1 {
  background-color: rgba(189, 26, 145, 0.62);
  top: 0;
  left: -55px;
}
.circle2 {
  background-color: rgba(26, 149, 15, 0.62);
  bottom: 0;
  left: 130px;
}
.circle3 {
  background-color: rgba(189, 60, 26, 0.62);
  top: 0;
  right: 55px;
}
